import React from 'react'
import {
    Box,
    FormControl,
    FormControlLabel,
    FormLabel,
    Grid,
    Paper,
    Radio,
    RadioGroup,
    TextField
} from "@material-ui/core";
import useStyles from "./Styles"

const Form = (props) => {
    const classes = useStyles();

    return (<form autoComplete={"off"}>
        <Box mt={4}>
            <Grid container justify="center">
                <Grid item xs={12} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                        <TextField
                            name={"numberOne"}
                            label={"First number"}
                            variant={"outlined"}
                            type={"number"}
                            defaultValue={"0"}
                            onChange={props.handler}
                        />
                    </Paper>
                </Grid>
                <Grid item xs={12} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                        <FormControl component={"fieldset"}>
                            <FormLabel component={"legend"}>Operation</FormLabel>
                            <RadioGroup aria-label={"operation"} name={"operation"} value={props.operationValue}
                                        onChange={props.handler}>
                                <FormControlLabel value={"+"} control={<Radio/>} label="+"/>
                                <FormControlLabel value={"-"} control={<Radio/>} label="-"/>
                                <FormControlLabel value={"*"} control={<Radio/>} label="ร"/>
                                <FormControlLabel value={"/"} control={<Radio/>} label={"รท"}/>
                            </RadioGroup>
                        </FormControl>
                    </Paper>
                </Grid>
                <Grid item xs={12} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                        <TextField name={"numberTwo"}
                                   label={"Second number"}
                                   variant={"outlined"}
                                   type={"number"}
                                   defaultValue={"0"}
                                   onChange={props.handler}/>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    </form>)
}

export default Form